import vendorPrefixes from '../constants/vendor-prefixes';

export function bindSpeechRecognition() {
  const [speechRecognition] = vendorPrefixes.filter(vendorPrefix => window[vendorPrefix]);

  console.log(speechRecognition);

  if (!speechRecognition) {
    throw Error('SpeechRecognition not supported in this browser');
  }

  // const speechRecognition = new webkitSpeechRecognition();
}
